import 'rxjs/Rx';
import { Injectable } from '@angular/core';
import { Logger } from 'angular2-logger/core';

import { sendRequest } from './api.service';

function padWithZeroes(text, length): string {
    let pad: string = "";
    if (text.length < length) {
        let padsWanted: number = length - text.length;
        for (let index = 0; index < padsWanted; index++) {
            pad += "0";
        }
    }

    return pad + text;
}

//For firefox to understand what .click(); is.
HTMLElement.prototype.click = function() {
   var evt = this.ownerDocument.createEvent('MouseEvents');
   evt.initMouseEvent('click', true, true, this.ownerDocument.defaultView, 1, 0, 0, 0, 0, false, false, false, false, 0, null);
   this.dispatchEvent(evt);
} 

function getCurrentDate(): string {
    let now: Date = new Date(Date.now());
    let thisYear: number = now.getFullYear();
    let thisMonth: number = now.getMonth() + 1;
    let thisDay: number = now.getDate();
    let today: string
            = thisYear.toString()
            + "-"
            + padWithZeroes(thisMonth.toString(), 2)
            + "-"
            + padWithZeroes(thisDay.toString(), 2);

    return today;
}

@Injectable()
export class VineExportService {
    private evaluationsEndpoint: string = "/export/evaluations.csv";

    constructor(private logger: Logger) {
    }

    exportCsv(vineCodes: string[]): Promise<boolean> {
        // Export information for the vines we want to CSV.

        // Returns a promise that is true when we're finished.
        return sendRequest(
                this.evaluationsEndpoint,
                vineCodes,
                "application/csv",
                "POST"
        ).then((response) => {
            return response.text();
        }).then((data) => {
            let link = document.createElement("a");
            link.download = getCurrentDate() + "_vine-data-export.csv";
            // from https://halistechnology.com/2015/05/28/use-javascript-to-export-your-data-as-csv/
            let csvData = encodeURI("data:text/csv;charset=utf-8," + data);
            link.href = csvData;
            link.click();
            return true;
        }).catch((error) => {
            this.logger.info("Vine Export: problem exporting vine data: " + error);
            return true;
        });
    }
}


