import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Logger } from 'angular2-logger/core';

import { VineRepository } from '../../repositories/vine.repository';
import { AppTextareaComponent } from '../input/appTextarea.component';

@Component({
    moduleId: module.id,
    selector: 'lookup',
    templateUrl: 'lookup.component.html'
})
export class LookupComponent {
    lookupForm: FormGroup;
    submitting: boolean;

    constructor(
            private builder: FormBuilder,
            private vineRepo: VineRepository,
            private router: Router,
            private logger: Logger) {
        this.submitting = false;
        this.createForm();
    }

    createForm() {
        this.lookupForm = this.builder.group({
            codes: ['', Validators.required]
        });
    }

    onReset() {
        this.lookupForm.reset();
    }

    onSubmit() {
        this.submitting = true;
        if (this.lookupForm.status === "VALID") {
            this.vineRepo.loadVines(this.codes.value).then((done) => {
                this.submitting = false;
            });
            this.router.navigateByUrl("/export");
        } else {
            this.logger.info("Lookup: please complete the form before submitting.");
            this.submitting = false;
        }
    }

    get codes() {
        return this.lookupForm.get('codes');
    }
}



