class PedigreeItem {
    code: string;
    breederCrossCode: string;
}

class Location {
    barcode: string;
    blockName: string;
}

class Programme {
    name: string;
}

class Breeder {
    name: string;
}

export class Vine {
    uuid: string;
    pedigreeItem: PedigreeItem;
    currentLocation: Location;
    programme: Programme;
    breeder: Breeder;
    public code: string;

    constructor(vineInfo: any) {
        this.uuid = vineInfo["uuid"];
        this.code = vineInfo["code"];

        if ("pedigreeItem" in vineInfo) {
            let pedItem = vineInfo["pedigreeItem"];
            this.pedigreeItem = new PedigreeItem();
            this.pedigreeItem.code = pedItem["code"];
            this.pedigreeItem.breederCrossCode = pedItem["breederCrossCode"];
        }
        if ("currentLocation" in vineInfo) {
            let location = vineInfo["currentLocation"];
            this.currentLocation = new Location();
            this.currentLocation.barcode = location["barcode"];
            this.currentLocation.blockName = location["blockName"];
        }
        if ("programme" in vineInfo) {
            let prog = vineInfo["programme"];
            this.programme = new Programme();
            this.programme.name = prog["name"];
        }
        if ("breeder" in vineInfo) {
            let breed = vineInfo["breeder"];
            this.breeder = new Breeder();
            this.breeder.name = breed["name"];
        }
    }
}


