import { Component, Renderer2, ViewChild, forwardRef, Input } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

export const APP_TEXTAREA_VALUE_ACCESSOR = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => AppTextareaComponent),
    multi: true
}

@Component({
    moduleId: module.id,
    selector: 'app-textarea',
    templateUrl: 'appTextarea.component.html',
    providers: [APP_TEXTAREA_VALUE_ACCESSOR]
})
export class AppTextareaComponent implements ControlValueAccessor {
    // A custom textarea that has its own template with the formatting we want.
    @ViewChild('textarea') textarea;
    @Input() labelText: string;
    @Input() helpText: string;
    @Input() formControl;
    onChange;

    userText: string;

    constructor(private renderer: Renderer2) {

    }

    writeValue(value: any): void {
        this.userText = value;
        const textControl = this.textarea.nativeElement;
        this.renderer.setProperty(textControl, 'textContent', value);
    }

    registerOnChange(func: any): void {
        this.onChange = func;
    }

    registerOnTouched(func: any): void {

    }

    change($event): void {
        let newValue = $event.target.value;
        this.userText = newValue;
        this.onChange(newValue);
    }

    setDisabledState(isDisabled: boolean): void {
        const action = isDisabled ? 'addClass' : 'removeClass';
        const textControl = this.textarea.nativeElement;
        this.renderer[action](textControl, 'disabled');
    }
}


