import { Vine } from './vine.model';

export class VineSearchResponse {
    // The response we get when we ask the API for a list of vines by code.

    // We add some things to it afterwards, so it's
    // more than just the response. It holds some
    // useful, related information that we can use,
    // such as the list of vines that we asked for.
    public codesRequested: string[];
    public vinesFound: Vine[];

    constructor(vineCodes: string[], vinesFound: Vine[]) {
        this.codesRequested = vineCodes;
        this.vinesFound = vinesFound;
    }

    numberRequested(): number {
        return this.codesRequested.length;
    }

    numberFound(): number {
        return this.vinesFound.length;
    }

    someNotFound(): boolean {
        return this.numberFound() !== this.numberRequested();
    }

    responseMessage(): string {
        let requestPlural: string = "s";
        let foundMessage: string
                = "only "
                + this.numberFound().toString()
                + " found.";
        if (this.numberRequested() === 1) {
            requestPlural = "";
        }
        if (this.numberFound() === 0) {
            foundMessage = "none found.";
        }

        return this.numberRequested().toString()
                + " vine"
                + requestPlural
                + " requested, but "
                + foundMessage;
    }
}


