function getHeaders(acceptType: string): Headers {
    // Return the headers that our API wants to see.
    let headers = new Headers();
    headers.append("Content-Type", "application/json");
    headers.append("Accept", acceptType);
    return headers;
}

function getPostBody(vineCodes: string[]): string {
    // Return the POST body that our API wants to see for the vines given.
    return JSON.stringify({"codes": vineCodes});
}

export function sendRequest(
    // Send a request to the API.
        endpoint: string,
        vineCodes: string[],
        acceptType: string,
        method: string): Promise<any> {
    let baseUrl: string = "http://dev.kakapo.pfr.co.nz:7080/vinesearch";

    return fetch(baseUrl + endpoint, {
        headers: getHeaders(acceptType),
        method: method,
        body: getPostBody(vineCodes)
    // }).then((response) => {
    //     return response.json();
    });
}


