import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { Logger } from 'angular2-logger/core';

import { Vine } from '../models/vine.model';
import { VineSearchResponse } from '../models/vineSearchResponse.model';
import { sendRequest } from '../services/api.service';

export function splitString(stringToSplit: string): string[] {
    // Split a string by space, tab, comma or all three.
    return stringToSplit.split(/[\s,]+/gm);
}

@Injectable()
export class VineRepository {
    private statusEndpoint: string = "/status";
    private vinesListEndpoint: string = "/vines";
    private vineEndpoint: string = "/vine";

    statusUrl: string;
    vinesListUrl: string;
    vineUrl: string;

    vineResponseSubject: Subject<VineSearchResponse> = new Subject<VineSearchResponse>();

    constructor(private logger: Logger) {
    }

    loadVines(vineCodeString: string): Promise<boolean> {
        // Look up and save a list of Vines to show the user.

        // Returns a promise which is true when we're finished.
        let vineCodes: string[] = splitString(vineCodeString);
        let vineRepo = this;

        return sendRequest(
                this.vinesListEndpoint,
                vineCodes,
                "application/hal+json",
                "POST"
        ).then((response) => {
            return response.json();
        }).then((body) => {
            if (body !== undefined) {
                let vinesFound: Vine[] = [];
                if ("_embedded" in body) {
                    let vineList = body["_embedded"]["vineList"];
                    for (let vineIndex in vineList) {
                        let vine = new Vine(vineList[vineIndex]);
                        vinesFound.push(vine);
                    }
                }

                let responseData: VineSearchResponse = new VineSearchResponse(
                        vineCodes, vinesFound);
                vineRepo.vineResponseSubject.next(responseData);
            }
            return true;
        }).catch((error) => {
            this.logger.info("Vine Repository: problem loading vines");
            return true;
        });
    }

    vineResponseSubscription(): Observable<VineSearchResponse> {
        // Give components an observable to subscribe to when vines are found.

        // Any component that subscribes to this method
        // will get an update whenever loadVines finds
        // new data
        return this.vineResponseSubject.asObservable();
    }
}


