import { Component } from '@angular/core';
import { Subscription } from 'rxjs/Rx';
import { Router } from '@angular/router';
import { Logger } from 'angular2-logger/core';

import { Vine } from '../../models/vine.model';
import { VineSearchResponse } from '../../models/vineSearchResponse.model';
import { VineRepository } from '../../repositories/vine.repository';
import { VineExportService } from '../../services/vineExport.service';

@Component({
    moduleId: module.id,
    selector: 'export',
    templateUrl: 'export.component.html'
})
export class ExportComponent {
    searchResponse: VineSearchResponse;
    rows;
    columns;
    selected;

    exporting: boolean;
    // keep track of our subscriptions
    // so we can unsubscribe from them and avoid
    // memory leaks
    subscriptions: Subscription[];

    constructor(
            private vineRepo: VineRepository,
            private exportService: VineExportService,
            public router: Router,
            private logger: Logger) {
        this.rows = [];
        this.columns = [
            {prop: 'code'},
            {name: 'Breeder'},
            {name: 'Position Barcode'},
            {name: 'Pedigree Item Code'},
            {name: 'Breeder Cross Code'}
        ];
        this.selected = [];
        this.exporting = false;
        this.subscriptions = [];
    }

    ngOnInit(): void {
        // get vines from the repository
        // whenever it finds any
        this.subscriptions.push(
            this.vineRepo.vineResponseSubscription().subscribe((vineResponse) => {
                // refresh the rows with our new data
                this.searchResponse = vineResponse;
                let newRows = [];
                let vinesList: Vine[] = vineResponse.vinesFound;
                for (let vineIndex in vinesList) {
                    let vine: Vine = vinesList[vineIndex];
                    let row = {
                        code: vine.code,
                        breeder: vine.breeder.name,
                        positionBarcode: vine.currentLocation.barcode,
                        pedigreeItemCode: vine.pedigreeItem.code,
                        breederCrossCode: vine.pedigreeItem.breederCrossCode
                    };
                    newRows.push(row);
                }
                this.rows = newRows;
                // copy all rows into selected
                // so everything starts off selected
                this.selected = this.rows.map((row) => {
                    return row;
                });
            }
        ));
    }

    ngOnDestroy(): void {
        // unsubscribe from all subscriptions
        this.subscriptions.map(sub => {
            sub.unsubscribe();
        });
    }

    onSelect({selected}) {
        this.selected.splice(0, this.selected.length);
        this.selected.push(...selected);
    }

    someVinesNotFound(): boolean {
        // Were some requested vines not found?
        if (this.searchResponse
        && this.searchResponse.someNotFound()) {
            return true;
        }

        return false;
    }

    canExport(): boolean {
        return this.selected.length > 0;
    }

    exportData(): void {
        this.exporting = true;
        let vineCodes: string[] = this.selected.map((vine: Vine) => {
            return vine.code;
        });
        this.exportService.exportCsv(vineCodes).then((done) => {
            this.exporting = false;
        });
    }
}



